.app-wrapper {
    margin: 0;
    display: grid;
    grid-template-areas:
            "h h"
            "c c"
            "f f f";
    grid-tempate-rows: 1fr 1fr 1fr 1fr;
    grid-template-columns: 12fr;

}

.header{
    grid-area: h;
}


.homePage {
    grid-area: c;
}

.footer {
    grid-area: f;
}
.logotip{
    width: 1200px;
    height: 800px;
}
.container{
    width: auto;
    height: 100%;
    background-image: url("https://million-wallpapers.ru/wallpapers/3/22/493627384134734.jpg");
}